/* Barra de Navegación y enlaces */
.navegacion-principal {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
.navegacion-principal a {
  position: relative;
  display: block;
  color: #FFFFFF;
  font-family: "Cinzel", serif;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 1rem;
  font-size: 1.4rem;
  padding-bottom: 2px;
}
.navegacion-principal a:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, #EEAD31, #FFFFFF);
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
  .navegacion-principal a {
    font-size: 2rem;
    margin-right: 2rem;
  }
}

/* Línea debajo de la navegación */
.navegacion-principal a:hover::before {
  transform: scaleX(1);
}

.navegacion-principal[data-animation=to-left] a::before {
  transform-origin: right;
}

.navegacion-principal[data-animation=center] a::before {
  transform-origin: center;
}

